/* top */

.top-20 {
  margin-top: 20px;
}

.top-35 {
  margin-top: 35px;
}

.top-60 {
  margin-top: 60px;
}

/* bottom */

.bottom-20 {
  margin-bottom: 20px;
}

/* side */

.side-25 {
  padding-left: 25px;
  padding-right: 25px;
}

.left-side-25 {
  padding-left: 25px;
}

.right-side-25 {
  padding-right: 25px;
}

/* width */

.full-width {
  width: 100%;
}

/* height */

.height-60 {
  height: 60px;
}

.height-66 {
  height: 66px;
}

/* flex */

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.center-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-between {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.v-center {
  display: flex;
  align-items: center;
}

.grow {
  flex-grow: 1;
}



